/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/

/* Buttons */

	button,
	input[type="button"],
	input[type="reset"],
	input[type="submit"] {
		border: 2px solid;
		border-color: $color_brand-green;
		border-radius: 3px;
		background: $color_background-main;
		color: $color_brand-green;
		font-size: .75rem;
		line-height: 1;
		padding: .6rem 1rem .4rem;

		&:hover {
			border-color: #ccc #bbb #aaa;
		}

		&:active,
		&:focus {
			border-color: #aaa #bbb #bbb;
		}
	}

	/* Fields */

	input[type="text"],
	input[type="email"],
	input[type="url"],
	input[type="password"],
	input[type="search"],
	textarea {
		color: $color__background-body;
		background: $color_background-main;
		border: 2px solid $color_brand-green;
		border-radius: 3px;

		&:focus {
			border: 2px solid $color_brand-green;
		}
	}

	input[name="quote"] {
		width: 100%;
		height: 75px;
		margin-top: 2%;
		margin-bottom: 2%;
	}

	input[name="author"],
	input[name="quote-source"],
	input[name="quote-source-url"]{
		width: 75%;
		margin-top: 2%;
		margin-bottom: 2%;
	}

	.fa-search{
		color: $color_brand-green;
		padding-left: 2%;
	}


	textarea {
		padding-left: 3px;
		width: 100%;
	}

